.information,
.information__item {
    display: flex;
    padding: 0;
    margin: 0;
}

.information__item-link {
        display: block;
        font-family: $font;
        font-weight: bold;
}

@media (min-width: $min-tablet-width) {
    .information {
        min-width: 510px;
        max-width: 800px;
        margin-top: 17px;
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: wrap;
    }

    .information,
    .information__item {
    padding-top: 15px;
}

    .information__item-link {
        width: 100px;
        padding-top: 8px;
        padding-bottom: 8px;
        margin-right: 25px;
        font-size: 20px;
        line-height: 26px;
        text-align: left;
    }
}

@media (min-width: $min-mobile-width) and (max-width: $max-mobile-width) {
    .information {
        position: relative;
        flex-direction: column;
        justify-content: flex-start;
        flex-wrap: nowrap;
        min-width: 70%;
        padding-top: 11px;
        padding-bottom: 28px;
        border-bottom: 1px dashed $orange;

        &::after {
            position: absolute;
            display: block;
            content: "";
            width: 100%;
            height: 3px;
            left: 0%;
            bottom: -5px;
            border-top: 1px dashed $orange;
            border-bottom: 1px dashed $orange;
        }
    }

    .information__item-link {
        color: $white;
        font-size: 17px;
        line-height: 22px;
        text-align: left;
        padding-top: 3px;
        padding-bottom: 3px;
    }
}
